import React, { useState, useEffect } from 'react';
import { ResultCard } from './ResultCard';

export const Add = () => {
	const [query, setQuery] = useState("");
	const [results, setResults] = useState([]);

	const onChange = (e) => {
		e.preventDefault();
		setQuery(e.target.value);

		fetch(`https://api.themoviedb.org/3/search/movie?api_key=6afec7f9b84e6f77a69e8f64e75c1985&language=en-US&page=1&include_adult=false&query=${e.target.value}`)
		.then((results) => results.json())
		.then((data) => {
			if(!data.errors) {
				setResults(data.results);
			} else {
				setResults([]);
			}
		});
	}

	return (
		<div className="add-page">
			<div className="container">
				<div className="add-content">
					<div className="input-wrapper">
						<input type="text" placeholder="Search movie..." value={query} onChange={onChange} />
					</div>
					{results.length > 0 && (
						<ul className="results">
							{results.map((movie) => (
								<li key={movie.id}>
									<ResultCard movie={movie} />
								</li>
							))}
						</ul>
					)}
				</div>
			</div>
		</div>
	)
}

